<template>
  <div class="mineLi">
    <ul :style="mineUl" v-if="liArray.length>0">
      <li v-for="(allLi,index) in allLis" :key="index" :style="mineLiStyle" @click=clickLi(index)
      :class="liArray[index].isOpen?'on':''">
        <div class="flag" v-if="liArray[index].isMark">🚩</div>
        <div class="show" v-if="liArray[index].isOpen">
          <div class="num" v-if="liArray[index].value!==9"><p v-if="liArray[index].value!==0">{{liArray[index].value}}</p></div>
          <div class="mines" v-else>💔</div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'mineLi',
  props:['liData','liArray'],
  computed:{
    mineUl(){
      return{
        width:this.liData.cross*(this.liData.liWidth+this.liData.liBorder*2)+'px',
        height:this.liData.vertical*(this.liData.liWidth+this.liData.liBorder*2)+'px',
      }
    },
    allLis(){
      return  this.liData.cross*this.liData.vertical;
    },
    mineLiStyle(){
      return{
        width:this.liData.liWidth+'px',
        height:this.liData.liWidth+'px',
        border:this.liData.liBorder+'px solid #333'
      }
    }
  },
  methods:{
    clickLi(e){
        this.$emit('clickLi',{index:e});
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
ul{
  padding: 0;
}
ul:after{
  content:'';
  display:block;
  clear:both;
}
li{
  list-style: none;
  float: left;
  width: 20px;
  height: 20px;
  background-color:#f1f1f1;
}
li{
  display:-webkit-flex;
  display:-moz-flex;
  display:-o-flex;
  display:flex;
  align-items:center;
  justify-content:center;
}
li div{
  width: 100%;
  line-height: normal;
}
li.on{
  background-color: #fff;
}
p{
  margin:0;
}
</style>



